import React, { useState } from 'react';
import './topbar.css';
import { Link } from 'react-router-dom';
import { FaBars, FaTimes } from 'react-icons/fa';
import LanguageContext, { useLanguage } from '../language/LanguageContext.js';


export default function TopBar() {
    const [click, setClick] = useState(false);

    const handleClick = () => setClick(!click);
    const closeMenu = ()=> setClick(false);

    const [currentLanguage, setLanguage] = useLanguage();
    const navItems = currentLanguage.navItems;
    const toggleButton = currentLanguage.buttonText;
    const handleToggle = () =>{
        if(currentLanguage.language==='EN'){
            setLanguage('cn');
            closeMenu();
        } else{
            setLanguage('en');
            closeMenu();
        }
    }

    return (
        <>
            <div className="navbar">
                <div className="navbar-container container">
                <Link to='./' className="navbar-logo" onClick={closeMenu}>
                    <i className="topIcon fab fa-battle-net"></i>
                </Link>
                <div className="menu-icon" onClick={handleClick}>
                    {click? <FaTimes/> : <FaBars/>}
                </div>
                <ul className={click ? 'nav-menu active' : 'nav-menu'}>
                    <li className="nav-item">
                        <div className="nav-links">
                            <button className="nav-button" onClick={handleToggle}>{toggleButton}</button>
                        </div>
                    </li>
                    <li className="nav-item">
                        <Link to='./' className="nav-links" onClick={closeMenu}>
                            {navItems[0]}
                        </Link>
                    </li>
                    <li className="nav-item">
                        <Link to='./details' className="nav-links" onClick={closeMenu}>
                            {navItems[1]}
                        </Link>
                    </li>
                </ul>
                </div>
            </div>
         </>   
    )
}
