import React, { createContext, useState, useContext } from "react";
import timelineElements from "../experience/timelineElements";

export const LanguageContext = createContext();
const languageOption = {
  en: {
    language:'EN',
    buttonText:'中文',
    headerTitle:['Zheng Lyu','Blog'],
    skills:'SKILLS',
    navItems:['Home', 'Gallery'],
    workExperience: timelineElements[0]
  },
  cn: {
    language:'CN',
    buttonText:'EN',
    headerTitle:['吕政','博客'],
    skills:'技术',
    navItems:['主页', '相册'],
    workExperience: timelineElements[1]
  }
};

export function LanguageProvider({ children }) {
  const [language, setLanguage] = useState("en");
  return (
    <LanguageContext.Provider
      value={[languageOption[language], setLanguage]}
    >
      {children}
    </LanguageContext.Provider>
  );
}

export const useLanguage = ()=> {
  return useContext(LanguageContext);
}